.message {
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   row-gap: 5px;
}

.imageAndText {
   display: flex;
}

.imageAndText img {
   order: 2;
   align-self: flex-end;
   max-width: 100%;
   height: 48px;
   width: 48px;
}

.text {
   position: relative;
   margin-right: 15px;
   padding: 10px;
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   max-width: 527px;
   min-width: 100px;
   border-radius: 10px;
   order: 1;
   background: #0066CC;
   box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.text:after {
   z-index: -1;
   position: absolute;
   right: -10px;
   bottom: 0px;
   content: '';
   border: 10px solid transparent;
   border-bottom: 10px solid #0066CC;
   border-left: 20px solid #0066CC;
}

.name {
   font-family: 'Montserrat', sans-serif;
   font-weight: 600;
   font-size: 18px;
   line-height: 22px;
   color: #FFFFFF;
}

.messageText {
   font-family: 'Montserrat', sans-serif;
   font-weight: 400;
   font-size: 16px;
   line-height: 20px;
   color: #FFFFFF;
}

.time {
   margin-right: 10px;
   font-family: 'Montserrat', sans-serif;
   font-weight: 600;
   font-size: 10px;
   line-height: 12px;
   color: #000000;
}