.sendForm {
   margin-top: 24px;
   display: grid;
   grid-template-columns: 1fr 120px;
   column-gap: 45px;
   flex-grow: 0;
}

.textarea {
   font-size: 14px;
   font-family: 'Montserrat', sans-serif;
   font-weight: 500;
   padding: 15px;
   height: 40px;
   background: #F5F7FB;
   box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
   border-radius: 20px;
   overflow : hidden;
}

.button {
   padding: 10px;
   align-self: flex-start;
   background: #0066CC;
   border-radius: 20px;
   border: none;
   font-family: 'Montserrat', sans-serif;
   font-weight: 500;
   font-size: 16px;
   line-height: 20px;
   color: #FFFFFF;
}

.button:hover {
      background-color: #2b8ef8;  
}
.button:active {
   background-color: #035bb9;
}
