.friendMessage {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   row-gap: 5px;
}

.friendImageAndText {
   display: flex;
   transform: scale(-1, 1);
}

.friendImageAndText img {
   order: 2;
   align-self: flex-end;
   max-width: 100%;
   height: 48px;
   width: 48px;
}

.friendText {
   position: relative;
   margin-right: 15px;
   padding: 10px;
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   max-width: 527px;
   border-radius: 10px;
   order: 1;
   background: #F5F7FB;
   box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {
   z-index: -1;
   position: absolute;
   right: -10px;
   bottom: 0px;
   content: '';
   border: 10px solid transparent;
   border-bottom: 10px solid  #F5F7FB;
   border-left: 20px solid  #F5F7FB;
}

.friendName {
   transform: scaleX(-1);
   font-family: 'Montserrat', sans-serif;
   font-weight: 600;
   font-size: 18px;
   line-height: 22px;
   color: #000000;
}

.friendMessageText {
   transform: scaleX(-1);
   font-family: 'Montserrat', sans-serif;
   font-weight: 400;
   font-size: 16px;
   line-height: 20px;
   color: #000000;
}

.friendTime {
   margin-left: 10px;
   font-family: 'Montserrat', sans-serif;
   font-weight: 600;
   font-size: 10px;
   line-height: 12px;
   color: #000000;
}